<template>
  <div
    class="slice-controls-container"
    :style="{
      left: `${left}px`,
      width: `${right - left}px`,
    }"
  >
    <div
      class="slice-controls-container__slice-controls slice-controls slice-controls--visual"
      :style="{
        left: `${visualLeft}px`,
        right: `${visualRight}px`,
        width: 'auto',
      }"
    >
      <div class="slice-controls__btn slice-controls__btn--left slice-controls__btn--visual">
        &laquo;
      </div>
      <div class="slice-controls__btn slice-controls__btn--right slice-controls__btn--visual">
        &raquo;
      </div>
    </div>

    <div class="slice-controls-container__slice-controls slice-controls slice-controls--hidden">
      <div
        draggable="true"
        class="slice-controls__btn slice-controls__btn--left"
        @dragend.passive="onDragEndLeft"
        @drag.passive="onDragLeft"
      ></div>
      <div
        draggable="true"
        ref="sliceControls"
        class="slice-controls__btn slice-controls__btn--right"
        @dragend.passive="onDragEndRight"
        @drag.passive="onDragRight"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SliceControls',
  props: {
    left: {
      type: Number,
      required: true,
    },
    right: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      visualRight: 0,
      visualLeft: 0,
    };
  },
  watch: {
    left() {
      this.visualLeft = 0;
    },
    right() {
      this.visualRight = 0;
    },
  },
  methods: {
    onDragEndLeft() {
      this.$emit('update-left', this.left + this.visualLeft);
    },
    onDragEndRight() {
      this.$emit('update-right', this.right - this.visualRight);
    },
    onDragLeft(event) {
      if (event.x === 0) {
        // workaround for browser weird behavior when drag event has 0 x coord
        // before drag end
        return;
      }
      this.visualLeft = event.offsetX;
    },
    onDragRight(event) {
      if (event.x === 0) {
        // workaround for browser weird behavior when drag event has 0 x coord
        // before drag end
        return;
      }
      this.visualRight = -event.offsetX;
    },
  },
};
</script>

<style scoped>
.slice-controls-container {
  position: relative;
  z-index: 0;
}

.slice-controls-container__slice-controls {
  position: absolute;
  top: 0;
  height: 100%;
  left: 0;
  width: 100%;
}

.slice-controls {
  display: flex;
  justify-content: space-between;
}

.slice-controls--visual {
  background-color: grey;
  opacity: 0.5;
  z-index: 0;
}

.slice-controls--hidden {
  z-index: 10;
}

.slice-controls__btn {
  width: 50px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.slice-controls__btn--visual {
  background-color: black;
  color: white;
}
</style>
