<template>
  <div class="container">
    <slice-controls
      class="controls"
      :left="left"
      :right="right"
      @update-right="onUpdateRight"
      @update-left="onUpdateLeft"
    />
  </div>
</template>

<script>
import SliceControls from '@/components/SliceControls';

export default {
  name: 'Demo',
  components: { SliceControls },
  data() {
    return {
      left: 200,
      right: 800,
    };
  },
  methods: {
    onUpdateRight(x) {
      this.right = x;
    },
    onUpdateLeft(x) {
      this.left = x;
    },
  },
};
</script>

<style scoped>
.container {
  background-color: yellow;
  width: 100%;
  height: 400px;
  position: relative;
}

.controls {
  top: 0;
  height: 100%;
}
</style>
